import { $next } from "./https";

/**
 * 
 * @returns {Object} 
 * @returns {number} returns.code 
 * @returns {Object} returns.data 
 * @returns {Array} returns.data.list 
 * @returns {Object} returns.data.page 
 * @returns {string} returns.msg 
 */
export const getRss = () => {
    return $next().get("/api/next2/rss/list");
}

/**
 * 
 * @param {string} id id
 * @returns {Object} 
 * @returns {number} returns.code 
 * @returns {Object} returns.data 
 * @returns {boolean} returns.data.subscribed 
 * @returns {number} returns.data.total 
 * @returns {string} returns.msg 
 */
export const getAuthorRss = ({id}) => {
    return $next({mute: true}).get(`/api/next2/rss/overview/author/${id}`);
}

/**
 * 
 * @param {string} id id
 * @returns {Object} 
 * @returns {number} returns.code 
 * @returns {Object} returns.data 
 * @returns {boolean} returns.data.subscribed 
 * @returns {number} returns.data.total 
 * @returns {string} returns.msg 
 */
export const getCommunityRss = ({id}) => {
    return $next({mute: true}).get(`/api/next2/rss/overview/community/${id}`);
}

